/* eslint-disable no-lone-blocks */
import { Box, Grid} from '@mui/material'
import Arrow, { DIRECTION } from 'react-arrows'

import 'katex/dist/katex.min.css';
import { InlineMath } from 'react-katex';

import './Mod2SteenrodSquares.css';
import Mod2FreeResolutionData from '../../data/mod5_free_resolution_sphere.json';
import { forwardRef } from 'react';

function generatorToString (generatorStr) {
    var subfixStr = '\\alpha^{' + generatorStr.split(';')[1].split(', ')[0] + '}_{' + generatorStr.split(';')[1].split(', ')[1] + ',' + generatorStr.split(';')[1].split(', ')[2] + '}';
    var cohomologyOperationStr = generatorStr.split(';')[0].replace(/beta/g, '{\\beta}').replace(/P/g, '\\mathrm{P}');

    if(generatorStr.split(';')[0] == "1"){
        subfixStr = '{ \\color{orange}' + subfixStr + '}'
    }

    cohomologyOperationStr = cohomologyOperationStr.replace(/\^[0-9]{0,256}/gm, (match, p1, offset) => `\^\{${match.split('^')[1]}\}`)

    return cohomologyOperationStr + '\\cdot' + subfixStr; // generatorStr + '@@' + 
}

function parseEvalArr (sqEntryArray, evalsArr) {
    var mappingArr = []

    for(let i=0;i<sqEntryArray.length;i++){
        var src = sqEntryArray[i];
        var dst = evalsArr[i];

        // eslint-disable-next-line no-loop-func
        dst.match(/[a-z|A-Z|0-9|\^|\s\+]{0,10000};.[0-9]{0,64},.[0-9]{0,64},.[0-9]{0,64}/gm).forEach((element) => {
            var rawElementArr = element.trim().split(';');

            var cohomologyOperationStr = rawElementArr[0].trim();
            var generatorStr = rawElementArr[1].trim();

            var cohomologyOperationsArr = cohomologyOperationStr.split('+');
            
            for (let j = 0; j < cohomologyOperationsArr.length; j++) {
                var cohomologyOperationTagStr = cohomologyOperationsArr[j].trim();

                var coefficientStr = cohomologyOperationTagStr.split(' ')[0].trim();
                if(coefficientStr.match(/[0-9\s]{0,10000}/gm).join('').length == 0)
                    coefficientStr = "1";
                
                mappingArr.push({
                    'from': src,
                    'to': cohomologyOperationTagStr + '; ' + generatorStr,
                    'multiplyBy': coefficientStr,
                })                
            }
        });
    }
    return mappingArr
}

const MAX_ROWS_TO_DISPLAY = 60;
const MAX_WIDTH = 60;
const MAX_UI_WIDTH = 16000;

export default function Mod2SteenrodSquares(props) {
    return (
        <>
            <Box minWidth={MAX_UI_WIDTH}>
                <Grid container className='grid_container'>
                    <Grid item xs={ 0.1 } className='row_header column_header'>
                    </Grid>
                    {
                        Array.from(Array(MAX_WIDTH).keys()).map((i) =>
                            {
                                return(
                                    <Grid item xs={ 0.198 } className="column_header">
                                        <InlineMath math = { i.toString() }/>
                                    </Grid>
                                );
                            }
                        )
                    }
                    {
                        Array.from(Array(MAX_WIDTH).keys()).map((i) =>
                            {
                                return(
                                    <>
                                        <Grid item xs={ 0.1 } className='row_header'>
                                            <InlineMath math = { i.toString() }/>
                                        </Grid>
                                        {
                                            Array.from(Array(MAX_WIDTH).keys()).map((j) =>
                                                {
                                                    if (j < MAX_ROWS_TO_DISPLAY - i) {
                                                        let sqEntryArray = Mod2FreeResolutionData[j][i];

                                                        return(
                                                            <Grid item xs={ 0.198 } className="container_table_entry" key = { 'Mod2SteenrodGridItem_' + j.toString() + '_' + i.toString() }>
                                                                <Box className="table_entry">
                                                                {
                                                                    sqEntryArray['list_ev_generators'].map((sq) => {
                                                                            return(
                                                                                <div id={ sq } key={ sq }>
                                                                                    <InlineMath math = { generatorToString(sq) }/>
                                                                                </div>
                                                                            )
                                                                        }
                                                                    )
                                                                }
                                                                </Box>
                                                            </Grid>
                                                        );
                                                    } else {
                                                        return(
                                                            <Grid item xs={ 0.198 } className="container_table_entry">
                                                                <Box className="table_entry">
                                                                </Box>
                                                            </Grid>
                                                        );
                                                    }
                                                }
                                            )
                                        }
                                    </>
                                );
                            }
                        )
                    }
                </Grid>
                {
                    Array.from(Array(MAX_WIDTH).keys()).map((i) =>
                        {
                            return(
                                <>
                                    {
                                        Array.from(Array(MAX_WIDTH).keys()).map((j) =>
                                            {
                                                if (1 <= j && j < MAX_ROWS_TO_DISPLAY - i) {
                                                    var sqEntryArray = Mod2FreeResolutionData[j][i]['list_ev_generators'];

                                                    var evalsArr = Mod2FreeResolutionData[j][i]['list_images'];
                                                    //if (evalsArr === undefined) return
                                                    var parsedEvalsArr = parseEvalArr(sqEntryArray, evalsArr)

                                                    return(
                                                        parsedEvalsArr.map((linkingItem) =>
                                                        {
                                                            return(
                                                                <Arrow
                                                                    className='arrow'
                                                                    from={{
                                                                    direction: DIRECTION.LEFT,
                                                                    node: () => document.getElementById(linkingItem.from),
                                                                    translation: [-.01, 0],
                                                                    }}
                                                                    to={{
                                                                    direction: DIRECTION.RIGHT,
                                                                    node: () => document.getElementById(linkingItem.to),
                                                                    translation: [.5, 0],
                                                                    }}
                                                                />
                                                            );
                                                        })
                                                    )
                                                }
                                            }
                                        )
                                    }
                                </>
                            )
                        }
                    )
                }
            </Box>
        </>
    );
}
